import React from "react";
import { ZpageLoading ,Zlayout} from "zerod";
import { Button } from "antd";
import HOC from "@/HOC/load-HOC.js";
const { AmdDocHOC } = HOC;
import doc from "zerod/components/ZpageLoading/doc.md";
export default AmdDocHOC(doc, {
	demo1: function() {
		class Myjavascript extends React.PureComponent {
            state={
                isShowLoading:false,
            }
            methods={
                onClick:()=>{
                    this.setState({
                        isShowLoading:true
                    })
                    setTimeout(() => {
                        this.setState({
                            isShowLoading:false
                        })
                    }, 5000);
                }
            }
			render() {
                return (
                    <Zlayout.Template>
                        <Button type="primary" onClick={this.methods.onClick}>显示loading</Button>
                         <ZpageLoading showLoading={this.state.isShowLoading}></ZpageLoading>
                    </Zlayout.Template>

                );
            }
		}
		return (
			<Myjavascript />
		);
	},
});
